const 角色 = z
    .object({
    身份: z.string().prefault('未设定'),
    负重: z
        .object({
        当前: z.coerce
            .number()
            .transform(value => Math.max(value, 0))
            .prefault(0),
        上限: z.coerce
            .number()
            .transform(value => Math.max(value, 1))
            .prefault(15),
    })
        .prefault({}),
    体温: z.coerce.number().prefault(36.5),
    力量: z.coerce
        .number()
        .transform(value => Math.max(value, 1))
        .prefault(3),
    敏捷: z.coerce
        .number()
        .transform(value => Math.max(value, 1))
        .prefault(3),
    体力: z.coerce
        .number()
        .transform(value => Math.max(value, 1))
        .prefault(3),
    抗体: z.enum(['F', 'E', 'D', 'C', 'B', 'A', 'S']).prefault('F'),
    创伤: z.string().prefault('健康'),
    上次过关日期: z.string().prefault(''),
    过关恢复期天数: z.coerce.number().prefault(14),
    体力恢复减半: z.boolean().prefault(false),
    技能: z
        .record(z.string().describe('技能名'), z.object({
        等级: z.coerce
            .number()
            .transform(value => Math.max(value, 1))
            .prefault(1),
        描述: z.string().prefault(''),
    }))
        .prefault({}),
    背包: z
        .record(z.string().describe('物品名'), z.object({
        数量: z.coerce.number().prefault(1),
    }))
        .transform(data => _.pickBy(data, ({ 数量 }) => 数量 > 0))
        .prefault({}),
    // 结局标记（系统管理，AI不更新）
    _已触发结局: z.boolean().prefault(false),
})
    .prefault({ 技能: {}, 背包: {} });
export const Schema = z.object({}).catchall(角色);
